body {
  margin: 0;
  padding: 0;
  background-color: #e8ecf1;
  font-family: 'Roboto', sans-serif;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  /* list-style: none; */
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

button {
  transition: 300ms ease;
  padding: 10px;
  cursor: pointer;
  background: #fff;
  text-transform: capitalize;
  letter-spacing: 1.2px;
  border-radius: 5px;
  margin: 20px;
}

button:hover {
  transition: 300ms ease;
  background: #86909d;
}

button:active {
  transition: 300ms ease;
  transform: scale(1.2);
}
